import { ChangeDetectorRef, Component } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { VhType } from 'src/app/sales/interface/vh-type';

@Component({
  selector: 'app-detail-category-products',
  templateUrl: './detail-category-products.component.html',
  styleUrls: ['./detail-category-products.component.scss']
})
export class DetailCategoryProductsComponent {
  list_product: any = [];
  loading: boolean = false;
  category: any;
  constructor(
    private router: Router,
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef
  ) {
    this.category = this.router.getCurrentNavigation().extras.state.category
  }
  ngAfterViewInit() {
    this.doRefreshData()
  }
  /**get data product */
  doRefreshData() {
    this.list_product =
    this.vhQuerySales.getlocalProducts(VhType.PRODUCT).filter(item => item.id_category == this.category._id).map(item =>{
      
      let arr:any = []  // sắp xếp subproduct theo tiền
      if(item.manysize && item.subs.length) {
        arr = this.vhAlgorithm.sortNumberbyASC(item.subs,'price')
        item['show_expand'] = false
        item.subs.forEach(element => {
          if(element.units){
            element['unit_exchange'] = element.units[0]
          }
        });
      }else{
        if(item.units){
          item['unit_exchange'] = item.units[0]
        }
      }
      return {
        ...item, 
      }
    })
  }
  heightScroll: string;
  ngAfterViewChecked() {
    if (document.querySelector("#detail-category-products") && document.querySelector(".ant-table-thead") && document.querySelector(".detail-category-products-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#detail-category-products").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".detail-category-products-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 80 + "px";
    }
    this.cdRef.detectChanges();
  }

  goBack() {
    this.router.navigate(['/sales/dashboard/manage/products/product-category']);
  }

   //** hàm sort cho các cột */
 productCol = false;          // chọn sort cột tên sản phẩm
 unitCol = false;             // Chọn sắp xếp theo đơn vị
 barcodeCol = false;          // Chọn sắp xếp theo barcode
 nameCategoryCol = false;     // Chọn sắp xếp theo tên danh mục
 brandCol = false;            // Chọn sắp xếp theo chi nhánh
 priceCol = false;            // Chọn sắp xếp theo giá
 sortTable(colName){
  switch (colName) {
    case 'name':
      if(this.productCol){
        this.list_product = this.vhAlgorithm.sortVietnamesebyASC([...this.list_product], colName)
      }else{
        this.list_product = this.vhAlgorithm.sortVietnamesebyDESC([...this.list_product], colName)
      }
      break;
    case 'unit':
      if(this.unitCol){
        this.list_product = this.vhAlgorithm.sortVietnamesebyASC([...this.list_product], colName)
      }else{
        this.list_product = this.vhAlgorithm.sortVietnamesebyDESC([...this.list_product], colName)
      }
      break;
    case 'barcode':
      if(this.barcodeCol){
        this.list_product = this.vhAlgorithm.sortVietnamesebyASC([...this.list_product], colName)
      }else{
        this.list_product = this.vhAlgorithm.sortVietnamesebyDESC([...this.list_product], colName)
      }
      break;
    case 'name_category':
      if(this.nameCategoryCol){
        this.list_product = this.vhAlgorithm.sortVietnamesebyASC([...this.list_product], colName)
      }else{
        this.list_product = this.vhAlgorithm.sortVietnamesebyDESC([...this.list_product], colName)
      }
      break;
    case 'brand':
      if(this.brandCol){
        this.list_product = this.vhAlgorithm.sortVietnamesebyASC([...this.list_product], colName)
      }else{
        this.list_product = this.vhAlgorithm.sortVietnamesebyDESC([...this.list_product], colName)
      }
      break;
    case 'price':
      if(this.priceCol){
        this.list_product = this.vhAlgorithm.sortNumberbyASC([...this.list_product], colName)
      }else{
        this.list_product = this.vhAlgorithm.sortNumberbyDESC([...this.list_product], colName)
      }
      break;
  }
 }
}
