import { VhComponent } from './../../../../components/vh-component/vh-component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { ProductCategoryComponent } from './product-category.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { DetailCategoryProductsComponent } from './detail-category-products/detail-category-products.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { IonicModule } from '@ionic/angular';
const routes: Routes = [
  {
    path: '',
    component: ProductCategoryComponent
  },
  {
    path: 'detail',
    component: DetailCategoryProductsComponent
  }
]

@NgModule({
  declarations: [
    ProductCategoryComponent,
    DetailCategoryProductsComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    ScrollingModule,
    NgZorroAntModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    IonicModule,
  ]
})
export class ProductCategoryModule { }
